html {
  scroll-behavior: smooth;
}

body {
  font-family: Rubik, sans-serif;
}

.container {
  width: 100%;
  max-width: 375px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1411px;
  }
}

button {
  cursor: pointer;
  border: none;
  display: block;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

input {
  border: none;
  outline: none;
}

* {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
}

address {
  font-style: normal;
}

.header {
  width: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0);
  padding: 36px 0;
  position: absolute;
  top: 0;
  left: 0;
}

.header .container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__pages {
  justify-content: space-between;
  align-items: center;
  gap: 50px;
  display: flex;
}

.header-btn {
  max-width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  align-items: center;
  justify-items: center;
  display: grid;
}

.header-btn__icon {
  width: 25px;
  height: 25px;
  fill: #fff;
}

.nav-list {
  display: none;
}

@media (min-width: 1024px) {
  .nav-list {
    width: 639px;
    opacity: 1;
    justify-content: center;
    align-items: center;
    gap: 31px;
    transition: opacity .25s ease-in-out;
    display: flex;
  }
}

.nav-list.is-hidden {
  opacity: 0;
}

.nav-list__item:nth-child(7) {
  justify-content: center;
  align-items: center;
  gap: 9px;
  display: flex;
}

.nav-list__item:nth-child(7) .nav-list__link {
  color: #fff;
  font-weight: 700;
}

.nav-list__link {
  text-transform: uppercase;
  color: rgba(255, 255, 255, .5);
  font-family: Rubik, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.93;
  transition: color .25s ease-in-out;
}

.nav-list__link:hover {
  color: #8856d9;
}

.nav-list__btn {
  max-width: 12px;
  max-height: 12px;
  background-color: rgba(0, 0, 0, 0);
  align-items: center;
  justify-items: center;
  display: grid;
}

.nav-list__icon {
  width: 12px;
  height: 12px;
  fill: #fff;
  transition: fill .25s ease-in-out;
}

.header-time {
  justify-content: center;
  align-items: center;
  gap: 19px;
  display: flex;
}

.header-time__title {
  color: #8c96a0;
  font-family: Rubik, sans-serif;
  font-size: 15px;
  font-weight: 400;
}

@media (min-width: 480px) {
  .header-time__title {
    font-size: 16px;
  }
}

.header-time-list {
  justify-content: center;
  align-items: center;
  gap: 12px;
  display: flex;
}

.header-time-list__item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.header-time-list__number {
  color: #a9c7c7;
  font-family: Rubik, sans-serif;
  font-size: 30px;
  font-weight: 700;
}

