.backdrop-buy {
  width: 100vw;
  height: 100vh;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  z-index: 9;
  opacity: 1;
  background-color: rgba(0, 0, 0, .5);
  transition: opacity .5s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop-buy.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.backdrop-buy.is-hidden .modal-buy {
  opacity: 0;
  transform: translateY(-20px);
}

.modal-buy {
  z-index: 10;
  max-width: 470px;
  min-height: 527px;
  background-color: #1c2023;
  background-image: url("modalmen.d9653767.png"), linear-gradient(rgba(79, 96, 152, 0) 63.61%, #30373f 100%);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 12px;
  padding: 10px;
  transition: transform .3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 480px) {
