.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  flex-direction: column;
  gap: 30px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
}
.footer-list {
  display: flex;
  gap: 18px;
  flex-direction: column;
  &__link {
    font-weight: 400;
    font-size: 16px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    z-index: 100;
    &:hover {
      color: getColors('white');
    }
  }
  @media screen and (min-width: 768px) {
    flex-direction: row;
  }
  @media screen and (min-width: 1200px) {
    flex-direction: row;
  }
}
.footer-language {
  text-transform: uppercase;
  color: getColors('grey-color');
  font-weight: 700;
  font-size: 14px;
  z-index: 100;
  &__en {
    color: getColors('white');
  }
}
.footer-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__text {
    text-align: center;
    color: getColors('grey-color');
    padding: 50px 0 37px 0;
    z-index: 100;
    @media screen and (min-width: 992px) {
      max-width: 904px;
    }
  }
  &__logo {
    width: 46px;
    height: 35px;
    z-index: 100;
  }
  &__year {
    padding: 18px 0 13px 0;
    color: getColors('grey-color');
    z-index: 100;
  }
  &__rights {
    color: getColors('grey-color');
    z-index: 100;
    &__link {
      color: getColors('button-purple');
    }
  }
}
